
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { getYMDFormateTime, isArrayType, getTimestamp, getHMFormateTime, getImageUrl } from "../../../js/utility.js"
import { mapState } from 'vuex'
import event from '../../../js/Event.js'
import PlateType from '../../../js/enum.js'
import { PLATFORM } from '../../../config.js'

export default {
name: 'fieldsign',
components: {
Group,
Cell,
Tabbar,
TabbarItem
},
data() {
return {
map: null,
address: "",
currentTime: "", //当前时间戳
customerName: "",
customerID: "",
};
},
beforeRouteLeave(to, from, next) {

if (to.name === 'office' || to.name === 'maindetail') {
this.$store.commit('updateDetail', '');
} else {
this.$store.commit('updateDetail', {
type: PlateType.SignIn,
address: this.address,
currentTime: this.currentTime,
customerName: this.customerName,
customerID: this.customerID,
});

event.$on('selectedRelateItem', function(data) {
this.handleSelectedInfo(data);
}.bind(this));
}
next();
},
beforeRouteEnter(to, from, next) {
if (from.name === 'relatelist') {
event.$off("selectedRelateItem");
}
next();
},
computed: {
...mapState([
'detail',
'detailView',
]),
specificlocation() {
if (this.address !== "") {
if (this.address.wktype === 'poi') {
return this.address.title;
} else if (this.address.wktype === 'defalut') {
return this.address.addressComponents.streetNumber;
}
}
return '';
},
bigtime() {
if (this.currentTime === "") { return ""; }
return getYMDFormateTime(this.currentTime);
},
smalltime() {
if (this.currentTime === "") { return ""; }
return getHMFormateTime(this.currentTime);
},
customerColor() {
if (this.customerName === '' && this.customerID === '') {
return '#DBDBDB';
}
return 'black';
}

},
mounted() {
if (this.detail !== '' && this.detail.type === PlateType.SignIn && this.detail.address !== '') {
this.currentTime = this.detail.currentTime;
this.customerName = this.detail.customerName;
this.customerID = this.detail.customerID;
this.address = this.detail.address;

var map = new BMap.Map("fieldmap");
this.map = map;
map.disableDragging(); 
map.enableScrollWheelZoom(false);
map.disableDoubleClickZoom();

map.centerAndZoom(this.address.point, 15);
this.addMarkerLabel(this.address.point);

} else {
this.currentTime = getTimestamp(new Date());
if (this.$route.params.action === 'customer' && this.detailView.type === 'fieldsigncustomer') {
this.customerName = this.detailView.name;
this.customerID = this.detailView.id;
}

var map = new BMap.Map("fieldmap");
this.map = map;
map.disableDragging(); 
map.enableScrollWheelZoom(false);
map.disableDoubleClickZoom();
var self = this;
this.$vux.loading.show({
text: '位置获取中'
});
if (PLATFORM !== 'web' && this.isAndroid()) { //this.isAndroid()&&navigator.geolocation
function successCallback(data) {
self.$vux.loading.hide();
var point = new BMap.Point(parseFloat(data.longitude), parseFloat(data.latitude));
map.centerAndZoom(point, 15);
self.addMarkerLabel(point);
self.getCity(point);

};

function failedCallback(data) {
self.$vux.loading.hide();

self.$vux.toast.text('定位失败', 'bottom');
};
baidu_location.getCurrentPosition(successCallback, failedCallback);
} else if (PLATFORM !== 'web' && this.isIOS()) {
var onSuccess = function(position) {
self.$vux.loading.hide();
var point = new BMap.Point(parseFloat(position.coords.longitude), parseFloat(position.coords.latitude));
var convertor = new BMap.Convertor();
var pointArr = [];
pointArr.push(point);
convertor.translate(pointArr, 1, 5, function(data) {
if (data.status === 0) {
var newpoint = data.points[0];
map.centerAndZoom(newpoint, 15);
self.addMarkerLabel(newpoint);
self.getCity(newpoint);
} else {
self.$vux.loading.hide();

self.$vux.toast.text('定位失败', 'bottom');
}
});
};

function onError(error) {
self.$vux.loading.hide();

self.$vux.toast.text('定位失败', 'bottom');
}

navigator.geolocation.getCurrentPosition(onSuccess, onError);
} else {
var geolocation = new BMap.Geolocation();
geolocation.getCurrentPosition(function(r) {
self.$vux.loading.hide();
if (this.getStatus() == BMAP_STATUS_SUCCESS) {
map.centerAndZoom(r.point, 15);
self.addMarkerLabel(r.point);
self.getCity(r.point);
} else {
self.$vux.toast.text('定位失败', 'bottom');
}
}, {
enableHighAccuracy: true,
maximumAge: 0
})
}
}

},
methods: {
isAndroid() {
var u = navigator.userAgent;
return u.indexOf('Android') > -1 || u.indexOf('Adr') > -1;
},
isIOS() {
var u = navigator.userAgent;
return !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);;
},
onIndexChange(newIndex, oldIndex) {
if (newIndex === 1) {
this.$router.replace({
name: 'teamsign'
})
}
},
addMarkerLabel(point) {
this.map.clearOverlays()
var myIcon = new BMap.Icon(require("../../../assets/sign_loc.png"), new BMap.Size(60, 60));
var marker2 = new BMap.Marker(point, { icon: myIcon }); 
this.map.addOverlay(marker2);
},
getCity(point) {
var geoc = new BMap.Geocoder();
var self = this;

geoc.getLocation(point, function(rs) {
var addComp = rs.addressComponents;
console.log(rs);
if (rs.surroundingPois.length > 0) {
var item = rs.surroundingPois[0];
item.wktype = 'poi';
self.address = item;






} else {





rs.wktype = 'defalut';
self.address = rs;

}

});
},
handleSelectedInfo(data) { //相关列表数据处理
this.detail.customerID = data.item.id;
this.detail.customerName = data.item.title;
this.$store.commit('updateDetail', this.detail);
},
onRelateClick() { //相关跳转列表
this.$router.push({
name: 'relatelist',
params: {
type: PlateType.Customer,
fromtype: PlateType.SignIn,
action: 'select',
section: '0',
row: '0',
addition: '0'
}
});
},
enterSign() {
if (this.address === '') { return; }
this.$router.push({
name: 'createsign'
});
},
enterMapChoice() {
if (this.address === '') { return; }
this.$router.push({
name: 'mapchoice'
});
}
}
};




